import React from 'react';

import { Helmet } from 'react-helmet'
import { Container, Row, Col, Image } from 'react-bootstrap';
import CardsInfo from './CardsInfo';

import thinkserver_partner_cards from './img/home/partner_cards/thinkserver.png';
import mikrotik_partner_cards from './img/home/partner_cards/mikrotik.png';
import bticino_partner_cards from './img/home/partner_cards/bticino.png';
import watchguard_partner_cards from './img/home/partner_cards/watchguard.png';

function Home() {
  return (
    <div>
      <Helmet>
        <title>Home - KlayHosting</title>
      </Helmet>

      <div className="pt-5 pb-2 contentContainer text-center">
        <span className="h1 text-white">
            Ospita qui il tuo<br />
            Server a prezzi imbattibili<br />
            con tecnologia Docker
        </span>
      </div>

      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 101"><path fill="#1A1A1A" fillOpacity="1" d="M0,100L1440,0L1440,0L0,0Z"></path></svg>

      <div>
        <h2 className="mt-5 text-center">Perché sceglierci?</h2>
        <hr className="mx-auto" style={{ width: '5%' }} />
        <Container>
          <Row>
            <Col>
              <CardsInfo title="Sicurezza" content="Tutti i nostri sistemi si basano su una solida e sicura struttura di rete certificata." />
            </Col>
            <Col>
              <CardsInfo title="Uptime garantito" content="Grazie alle infrastrutture scelte siamo in grado di garantirti uptime al top su base annuale (99.97%)." />
            </Col>
            <Col>
              <CardsInfo title="Prezzi competitivi" content="Offriamo il miglior servizio ad un prezzo contenuto rispetto ai nostri competitor presenti sul mercato." />
            </Col>
          </Row>
        </Container>

        <h2 className="mt-5 text-center">Noi utilizziamo</h2>
        <hr className="mx-auto" style={{ width: '5%' }} />

        <Container className="mt-5 mb-5">
          <Row>
            <Col className="d-flex align-items-center justify-content-center"><a href="https://www.lenovo.com/" rel="noreferrer" target="_blank"><Image src={thinkserver_partner_cards} fluid /></a></Col>
            <Col className="d-flex align-items-center justify-content-center"><a href="https://mikrotik.com/" rel="noreferrer" target="_blank"><Image src={mikrotik_partner_cards} fluid /></a></Col>
            <Col className="d-flex align-items-center justify-content-center"><a href="https://www.bticino.it/" rel="noreferrer" target="_blank"><Image src={bticino_partner_cards} fluid /></a></Col>
            <Col className="d-flex align-items-center justify-content-center"><a href="https://www.watchguard.com/" rel="noreferrer" target="_blank"><Image src={watchguard_partner_cards} fluid /></a></Col>
          </Row>
        </Container>
      </div>
    </div>
  )
}

export default Home;