import React from 'react';

import { Helmet } from 'react-helmet'
import { Container, Row, Col, Image } from 'react-bootstrap';
import CardsStaff from './CardsStaff';

import servers from './img/about/servers.jpg';
import kevin_staff_card from './img/about/staff_cards/kevin.png';
import alessio_staff_card from './img/about/staff_cards/alessio.png';
import stefan_staff_card from './img/about/staff_cards/stefan.png';

function About() {
  return (
    <div>
      <Helmet>
        <title>Chi siamo - KlayHosting</title>
      </Helmet>

      <div className="pt-5 pb-2 contentContainer text-center">
        <span className="h1 text-white">Chi siamo</span><br />
        <span className="text-muted">Per saperne di più su di noi</span>
      </div>

      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 101"><path fill="#1A1A1A" fillOpacity="1" d="M0,100L1440,0L1440,0L0,0Z"></path></svg>

      <Container className="mb-5">
        <Row>
          <Col xs={12}>
            <div className='mt-4'>
              <h2 className="text-center">Datacenter</h2>
              <hr className="mx-auto" style={{ width: '5%' }} />
              <p className="text-center">
                Il nostro Data Center è protetto contro le intrusioni fisiche da un sistema di allarme con sensori su tutti i varchi e collegato anche ai sistemi del servizio di vigilanza privata.<br />
                È presente anche un sistema di controllo accessi che registra per tutti i varchi gli accessi effettuati dalle persone abilitate, coadiuvato da un sistema di telecamere a circuito chiuso.<br />
                La temperatura di esercizio in tutti i locali tecnici è mantenuta a circa 22° da un gruppo di 3 condizionatori industriali canalizzabili e un sistema di rilevamento della temperatura interna segnala eventuali variazioni.<br />
                Ogni locale tecnico è protetto contro gli incendi da un sistema composto da una centrale a cui sono connessi i sensori di rilevamento a pavimento e a soffitto presenti in ogni locale tecnico e che pilota la parte di spegnimento composta da erogatori a polveri di potassio.<br />
                L'alimentazione elettrica viene fornita di norma tramite due linee dedicate in corrente alternata monofase 220V fino a 32A su prese industriali.<br />
                L'intero Data Center è protetto da un gruppo di continuità modulare e da un gruppo elettrogeno.<br />
                L’intero sistema è oggetto di backup schedulati e controllati.
              </p>
              <Image src={servers} fluid />

              <hr />

              <h2 className="mt-5 text-center">La nostra squadra</h2>
              <hr className="mx-auto" style={{ width: '5%' }} />
              <Container>
                <Row>
                  <Col>
                    <CardsStaff img={kevin_staff_card} title="Kevin" role="Founder & DEV" content="Imprenditore autonomo e motivato con una comprovata esperienza di successo e risultati straordinari nella costruzione di un'azienda dalla semplice idea alla sua realizzazione vera e propria. Esperienza in tutti gli aspetti dello sviluppo aziendale, inclusi finanza, operazioni, tecnologia e marketing. Capacità di leadership/comunicazione ben sviluppate, ottime capacità di gestione del tempo e grandi capacità di lavoro di squadra." tag="@Klay4#0909" />
                  </Col>
                  <Col>
                    <CardsStaff img={alessio_staff_card} title="Alessio" role="Co-founder & DEV" content="Programmatore esperto e innovativo capace di creare codici informatici e gestire efficacemente sistemi software interni. Capacità di collaborazione con designer e produttori di contenuti, creando codice per produrre applicazioni server multi-thread. Esperienza nel testing e documentazione di software per diverse applicazioni. Dedizione nel lavoro in team e professionista motivato." tag="@ale3497#0344" />
                  </Col>
                  <Col>
                    <CardsStaff img={stefan_staff_card} title="Stefan" role="Support Team Manager" content="Professionista del servizio clienti dedito ed esperto con svariati anni di esperienza di lavoro nel settore del servizio clienti per le tecnologie d'ufficio. Abilità di lavoro in ambienti dai ritmi rapidi ed esigenti, per fornire ai clienti il massimo livello di servizio clienti possibile. Orientamento agli obiettivi e impegno nella ricerca di soluzioni a qualsiasi problema. Esperienza comprovata nel mantenimento di una forte base di clienti e nell'attrazione di nuovi clienti." tag="@Killmee1#3276" />
                  </Col>
                </Row>
              </Container>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

export default About;